import { useState } from 'react';
import './App.css';
import greatings from './greatings';
import Svg from './Svg';
import * as htmlToImage from 'html-to-image'
import FlatList from 'flatlist-react';
// import { toJpeg } from 'html-to-image';

const steps = [
  { id: 1, title: 'background' },
  { id: 2, title: 'Pharse' },
  { id: 3, title: 'Text' },
  { id: 4, title: 'Name' },
  { id: 5, title: 'Download' },
]

const SvgsList = [
  
  ...(Array.from(Array(211).keys()).map(num => {
    return { id: num + 1, type: 'Eid' }
  })),
  ...(Array.from(Array(119).keys()).map(num => {
    return { id: num + 211 + 1, type: 'Ramadan' }
  })),
  ...(Array.from(Array(15).keys()).map(num => {
    return { id: num + 330 + 1, type: 'Jumma' }
  }))
];

let url = '/GreetingCards';
function App() {
  const [bg, setBg] = useState(-1);
  const [svg, setSvg] = useState(null);
  const [txt, setTxt] = useState('');
  const [name, setName] = useState('  ');
  const [step, setStep] = useState(1);
  const download = async () => {
    const ele = document.querySelector('.result');
    const fmc = await htmlToImage.getFontEmbedCSS(ele);
    console.log(fmc);
    htmlToImage.toPng(ele, { fontEmbedCSS: fmc })
      .then(function (dataUrl) {
        var link = document.createElement('a');
        link.download = 'aa greatings.jpg';
        link.href = dataUrl;
        link.click();
      });
  }
  const darker = () => {
    document.querySelector('.darker').classList.toggle('d-none');
  }
  const nav = (n) => {
    setStep(index => index + n);
  }
  const renderImageListItem = (id) => {
    return <div onClick={() => setBg(id)} className={`pick ${(bg) === id ? 'selected' : ''}`}>
      <img
        src={url+`/imgs/bg/${id + 1}.jpg`}
        className="w-100 shadow-1-strong rounded"
        alt=""
      ></img>
    </div>
  }
  const renderSvgListItem = (val) => {
    const { id, type } = val;
    return <div onClick={() => setSvg(val)} className={`pick ${svg && svg.id === id ? 'selected' : ''}`}>
      <img
        src={url+`/imgs/svg/${type}/${id}.svg`}
        className="w-100 shadow-1-strong rounded"
        alt=""
      ></img>
    </div>
  }
  // useEffect(() => {
  //   if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
  //     // dev code
  //   } else {
  //     // production code
  //     url = '/GreetingCards';
  //   }
  // }, [])

  return (
    <div className="container">
      <div className='d-flex flex-column'>
        <div className='sticky-top bg-white py-2 pt-3'>
          <h4 className='text-center text-dark'>AA : Greeting Cards</h4>
          <div className='py-2'>
            <ul class="steps">
              {steps.map(s => <li className={`step ${s.id === step ? 'step-active' : s.id < step ? 'step-success' : ''}`}>
                <div class="step-content" key={s.title}>
                  <span class="step-circle">{s.id}</span>
                  <span class="step-text">{s.title}</span>
                </div>
              </li>)}

            </ul>
          </div>
          <div className='py-2'>
            <div className="d-flex justify-content-between">
              <button type="button" class={`btn btn-secondary btn-sm ${step === 1 && 'disabled'}`} onClick={() => nav(-1)}>Previus</button>
              <button type="button" onClick={() => nav(1)} class={`btn btn-primary btn-sm${step === 5 || bg===-1? ' disabled' : ''}`}>Next</button>
            </div>
          </div>
        </div>
        <div className='flex-grow-1'>
          <div className={`${step === 1 ? '' : ' d-none'}`}>
            <FlatList list={Array.from(Array(44).keys())} renderItem={renderImageListItem} displayGrid />
          </div>
          <div className={`${step === 2 ? '' : ' d-none'}`}>
            <FlatList list={SvgsList} groubBy="type" renderItem={renderSvgListItem} displayGrid />
          </div>
          <div className={`row${step === 3 ? '' : ' d-none'}`}>
            <div class="mb-3">
              <label className="form-label">Write your own words..</label>
              <textarea value={txt} onChange={(e) => setTxt(e.target.value)} class="form-control form-control-sm text-center" rows="3" spellcheck="false"></textarea>
            </div>
            {greatings.map(val =>
              <div class={`pick col-lg-3 col-md-4 col-sm-6 mb-4`}>
                <div onClick={() => setTxt(val)} className={`rounded p-2 border h-100 d-flex justify-content-center align-items-center text-center ${val === txt ? 'selected' : ''}`} style={{ background: '#ffff' }}>
                  <p>{val}</p>
                </div>
              </div>)}
          </div>
          <div className={`row${step === 4 ? '' : ' d-none'}`}>
            <div class="mb-3">
              <label className="form-label">Name..</label>
              <input class="form-control form-control-sm text-center" value={name} onChange={(e) => setName(e.target.value)}></input>
            </div>
          </div>
          <div className={`${step === 5 ? '' : 'd-none'}`}>
            <div className="d-flex flex-column justify-content-center" >
              <div className='rounded shadow mx-auto' style={{ overflow: 'hidden' }}>
                <div className='result'>
                  <img src={url+`/imgs/bg/${bg + 1}.jpg`} alt="" />
                  <div className='d-none darker'></div>
                  <div className='content d-flex flex-column py-3'>
                    {step === 5 && svg && svg.id !== 0 && <Svg selectedSvg={svg} url={url} />}
                    <div className="flex-grow-1 text-center p-3 pt-5 txt">{txt}</div>
                    <div className="name py-3 text-center">{name}</div>
                  </div>
                </div>
              </div>
              <div className='mt-2 mx-auto'>
                <div className="btn btn-primary m-1" onClick={download}>Download</div>
                <div className="btn btn-dark m-1" onClick={darker}>Darker</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
