import React, { useState, useEffect } from 'react'
import './Svg.css';

export default function SVG({ selectedSvg, url }) {
    const { id, type } = selectedSvg;
    const furl = url + `/imgs/svg/${type}/${id}.svg`;
    const [svg, setSvg] = useState(null);
    useEffect(() => {
        if (furl)
            fetch(furl).then(res => res.text()).then(txt => setSvg(txt));
    }, [])
    return (
        <div className='text-center pt-4' dangerouslySetInnerHTML={{ __html: svg }} />
    )
}
